import { ReactNode } from 'react';
import { CalendarCardPlugin } from '@ali/4ever-bamboo';

type VoidFun = () => void;

export enum CalendarType {
  SHANHUI = 'shanhui',
}

export enum JumpToCalendarEventType {
  TITLE = 'title',
  ICON = 'icon',
  RECEIVER = 'receiver',
  DOUBLECLICK = 'doubleClick',
}

export type CalendarCardPluginConfig = {
  locale?: {
    refresh: string;
    refreshTips: string; // 日程卡片有更新
    receiverCount: string; // 等{count}人参会
    viewCalendardetails: string; // 查看日程详情
    noresponse: string; // 未响应
    received: string; // 已接收
    refused: string; // 已拒绝
    pending: string; // 待定
    organizer: string; // 组织人
    subjectPlaceholder: string; // 创建日程、会议、活动
    timePlaceholder: string; // 开始/截止时间
    participantPlaceholder: string; // 参会人
  };
  isMobile?: boolean;
  getContainerWidthInPC?: () => number;
  jumpToCalendar?: (link?: string, eventType?: JumpToCalendarEventType) => void;
  openProfile?: (uid: number) => void;
  jumpToAttachment?: (url: string) => void;
  refresh?: (calendarId: string, eventType?: 'click') => Promise<RefreshReponse>;
  onRefreshSuccess?: VoidFun;// 刷新成功回调
  // 会议纪要2期优化新增配置
  handleCreateCalendar?: (key: string) => void;
  renderCreateCalendarBtn?: (
    props: {
      key: string;
      initCalendar: (data: CalendarCardPlugin.CalendarCardMetadataType) => Promise<boolean>;
    },
  ) => ReactNode;
  /**
   * 初始化日程
   */
  onInitData?: () => void;
  mode?: CalendarType;
  lazyload?: any;
};

export interface RefreshReponse {
  status: 'success' | 'error';
  message?: string;
  data?: CalendarCardPlugin.CalendarCardMetadataType;
};

export interface CalendarInjectionType {
  injectionMetadata?: CalendarCardMetadataType;
  injectionHeight?: number;
  isFirstLoaded?: boolean;
}

export type CalendarCardDataType = CalendarCardPlugin.CalendarCardDataType;

export type CalendarCardMetadataType = CalendarCardPlugin.CalendarCardMetadataType;

export type CommonMouseEvent = CalendarCardPlugin.CommonMouseEvent;

export type IReceiver = CalendarCardPlugin.IReceiver;
